
.m-todos {
	float: left;
	width: 230px;
	height: 100%;
	background-color: #fff;
	border-right: 1px solid #cecece;
}
.m-todos p {
	height: 22px;
	padding: 8px 10px;
	background-color: #eee;
	border-bottom: 1px solid #cecece;
}
.m-todos p span {
	display: inline-block;
	padding: 3px 10px;
	border: 1px solid transparent;
	cursor: pointer;
}
.m-todos span.selected {
	background-color: #fff;
	border: 1px solid #cecece;
}
.m-todos li {
	padding: 9px 20px;
}
.m-todos .todos-item {
	cursor: pointer;
}
.m-todos .todos-item.completed {
	color: #2a8e0a;
}
.m-todos .todos-date {
	background-color: #fafaee;
}
.m-todos .selected {
	background-color: #f2f2f2;
}
.m-todos .todos-add {
	width: 174px;
	border-top: 1px solid #cecece;
	padding: 14px 28px;
	position: absolute;
	bottom: 0;
	background-color: #f2f2f2;
	cursor: pointer;
}
.todos-add span {
	margin-left: 10px;
	vertical-align: middle;
}



