body,
html {
    width: 100%;
    height: 100%;
    min-width: 960px;
    min-height: 640px;
    padding: 0;
    margin: 0;
    font: 12px/1.14 Arial, Hiragino Sans GB, \5fae\8f6f\96c5\9ed1, Helvetica, sans-serif;
    color: #333;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

p,
h1 {
    margin: 0;
}

.g-hd {
    width: 100%;
    height: 60px;
    color: #9a9a9a;
    background-color: #ddd;
    overflow: hidden;
    border-bottom: 1px solid #cecece;
}

.g-hd h1 {
    font-size: 20px;
    line-height: 15px;
    margin: 25px auto 20px 13px;
}

.g-bd {
    width: 100%;
    min-width: 960px;
    min-height: 640px;
    font-size: 16px;
    position: absolute;
    top: 61px;
    bottom: 0;
    overflow: hidden;
}


