
.m-detail {
	height: 100%;
	position: absolute;
	left: 462px;
	right: 0;
	background-color: #fff;
}
.m-detail .detail-title {
	width: 100%;
	line-height: 50px;
	height: 50px;
	background-color: #f2f2f2;
}
.m-detail h1 {
	display: inline-block;
	margin-left: 16px;
	font-size: 16px;
}
.m-detail .detail-date {
	width: 100%;
	line-height: 52px;
	height: 52px;
	background-color: #fafaee;
}
.m-detail p {
	margin-left: 16px;
}
.m-detail .detail-content {
	width: 100%;
	margin: 16px;
}
.m-detail .complete {
	width: 25px;
	height: 25px;
	background: url(../assets/img/sprite.png) -51px 0 no-repeat;
	float: right;
	margin: 12px 23px auto auto;
}
.m-detail .edit {
	width: 25px;
	height: 25px;
	background: url(../assets/img/sprite.png) -26px 0 no-repeat;
	float: right;
	margin: 12px 21px auto auto;
}



