
.detail-edit[data-v-3] {
	width: 400px;
	padding: 20px;
	position: absolute;
	top: 20px;
	left: 50%;
	transform: translateX(-50%);
	text-align: center;
	background-color: #f2f2f2;
	border: 1px solid #cecece;
}
.detail-edit h1[data-v-3] {
	width: 100%;
	margin: 10px 0;
	font-weight: normal;
	font-size: 18px;
}
.detail-edit label[data-v-3] {
	display: block;
}
.detail-edit p[data-v-3] {
	font-size: 12px;
	color: red;
	margin-left: 130px;
	text-align: left;
	height: 15px;
}
.detail-edit input[data-v-3],
.detail-edit textarea[data-v-3] {
	font-size: 16px;
	width: 190px;
	line-height: 30px;
	margin: 15px 25px;
	padding: 2px 6px;
	font-family: Arial, Hiragino Sans GB, \5fae\8f6f\96c5\9ed1, Helvetica, sans-serif;
	color: #333;
}
.detail-edit textarea[data-v-3] {
	line-height: 25px;
	color: #333;
	vertical-align: middle;
}
.detail-edit button[data-v-3] {
	display: inline-block;
	width: 80px;
	height: 30px;
	margin: 20px 30px 15px;
	font-size: 16px;
	font-family: Arial, Hiragino Sans GB, \5fae\8f6f\96c5\9ed1, Helvetica, sans-serif;
	color: #333;
}



