
.m-category {
	float: left;
	width: 230px;
	height: 100%;
	background-color: #f2f2f2;
	border-right: 1px solid #cecece;
}
.m-category p {
	margin: 10px 0 10px 12px;
}
.m-category>div {
	padding: 28px 0;
}
.m-category .category-all {
	margin-bottom: 20px;
}
.category-list {
	margin: 7px 0;
	cursor: pointer;
}
.category-list ul {
	margin: 7px 0;
}
.category-list .category-item {
	padding-left: 19px;
}
.category-list .category-item li {
	padding: 8px 0;
	padding-left: 44px;
	margin-left: -19px;
}
.category-list .category-item li.selected {
	background-color: #fff;
	background-origin: border-box;
}
.m-category .category-add {
	width: 174px;
	border-top: 1px solid #cecece;
	padding: 14px 28px;
	position: absolute;
	bottom: 0;
	cursor: pointer;
}
.category-add span {
	margin-left: 10px;
	vertical-align: middle;
}
i {
	display: inline-block;
}
.folder {
	width: 16px;
	height: 11px;
	background: url(../assets/img/sprite.png) 0 0 no-repeat;
	margin-right: 16px;
}
.file {
	width: 10px;
	height: 13px;
	background: url(../assets/img/sprite.png) -16px 0 no-repeat;
	margin-right: 11px;
}
.delete {
	height: 12px;
	width: 12px;
	visibility: hidden;
	position: relative;
	float: right;
	margin-right: 12px;
	cursor: pointer;
}
.category-item:hover .delete {
	visibility: visible;
}
.delete:before,
.delete:after {
	content: '';
	height: 3px;
	width: 12px;
	display: block;
	background: #333;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	position: absolute;
	top: 7px;
	left: 0px;
	transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
	-moz-transform: rotate(-45deg);
	-o-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
}
.delete:after {
	transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
	-moz-transform: rotate(45deg);
	-o-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
}
.add {
	height: 18px;
	width: 18px;
	position: relative;
	vertical-align: middle;
}
.add:before,
.add:after {
	content: '';
	height: 4px;
	width: 18px;
	display: block;
	background: #333;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	position: absolute;
	top: 6px;
	left: 0px;
}
.add:after {
	height: 18px;
	width: 4px;
	top: -1px;
	left: 7px;
}



